/*------------------------------------*\
    Typography
\*------------------------------------*/

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
    margin: 0 0 1rem;
    color: $c-font-db;
}

h1,
.h1 {
    @include f-lg();
}

h2,
.h2 {
    @include f-md();
}

h3,
.h3 {
    @include f-sm();
}

// h4, .h4 {

