// h5, .h5 {

// }

// h6, .h6 {

// }

p {
    margin: 0 0 1rem 0;
    @include f-reg();

    // &:last-of-type {
    //     margin-bottom: 0;
    // }
}

/*------------------------------------*\
    Additional utility classes
\*------------------------------------*/

.f-uc {
    text-transform: uppercase;
}

.f-bold {
    font-weight: 700;
}

.f-expanded-sm {
    letter-spacing: 0.03rem;
}

.f-expanded-md {
    letter-spacing: 0.04rem;
}

.f-expanded-lg {
    letter-spacing: 0.09rem;
}
